    text-shadow: black 4px 4px 8px;
    display: flex;
    /* justify-content:space-around; */
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    
}

.contact form,p{
    width: 70%;
}


form{
    /* width: 300px; */
    /* height: 350px; */
    /* border:black 2px solid; */
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    
    /* background-color: rgb(37, 37, 37); */
