/* {cart} */

.fa,
.fas {
  font-weight: 600;
  display: grid;
  place-items: center;
  font-size: 1.6rem;
  cursor: pointer;
}



.main-cart-section{

  background-color: rgb(41, 41, 41);
}
.main-cart-section h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 3rem;
  text-transform: capitalize;
  background-color: rgb(41, 41, 41);
  margin-bottom: 1rem;
  text-align: center;
  color: #ffe6a7;
  
}

.main-cart-section .total-items {
  font-style: normal;
  font-weight: normal;
  font-size: 1.8rem;
  text-transform: capitalize;
  background-color: rgb(41, 41, 41);
  margin-bottom: 2rem;
  
}

.total-items-count {
  font-weight: bold;
  font-size: 2rem;
  color: rgb(255, 95, 95);
}

/* cart main div start  */

.main-cart-section .cart-items {
  width: 100%;
  height: auto;
  background: linear-gradient(
    103.49deg,
    #ffffff -28.13%,
    rgba(242, 247, 255, 0.5) 116.84%
  );
  box-shadow: rgba(0, 0, 0, 0.08) 0rem 0.4rem 1.2rem;
  border-radius: 2rem;
  margin: auto;
  margin: auto;
  display: grid;
  place-items: center;
  padding-top: 1rem;
}

.cart-items hr {
  margin: 1rem auto;
  overflow-y: scroll;
}

.cart-items-container {
  width: 90%;
  height: 20rem;
 
}

.items-info {
  width: 100%;
  height: 7rem;
  /* background-color: lavender; */
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  border-bottom: 1px black solid;
}

.items-info .product-img img {
  width: 10rem;
  height: 6rem;
  filter: drop-shadow(0rem 0.4rem 1rem #f1f7ff);
  border-radius: 1rem;
}

.items-info .title {
  display: flex;
  justify-content:center;
  flex-direction: column;
}

.items-info .title h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: rgb(0, 0, 0);
}

.items-info .title p {
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  text-align: left;
  color: #000000;
}

.add-minus-quantity {
  display: flex;
  justify-content:flex-end;
  align-items: center;
}

.add-minus-quantity i{
  font-size: 1.2rem;
}

.add-minus-quantity button {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.add-minus-quantity input {
  width: 4rem;
  height: 2.2rem;
  border: 0.141rem solid #000000;
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 1.3rem;
  text-align: center;
  text-transform: capitalize;
  color: #000000;
  margin: 0 1rem;
  border-radius: 0.5rem;
  outline: none;
  background-color: transparent;
}

.add-minus-quantity input:focus {
  outline: none;
}

.price {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.price h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.6rem;
  text-transform: capitalize;
  color: rgb(0, 0, 0);
}

.remove-item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 5rem;
}

.remove-item button {
  border: none;
  background-color: transparent;
}

.remove-item i{
  font-size: 1.4rem;
}

.card-total {
  width: 95%;
  margin-top: 2rem;
  text-align: right;
}

.card-total h3 {
  font-style: 200;
  font-size: 2rem;
  line-height: 1rem;
  text-transform: capitalize;
  color: #f1f1f1;
}

.card-total h3 span {
  font-style: normal;
  font-weight: bold;
  font-size: 2.1rem;
  line-height: 3.2rem;
  text-transform: capitalize;
  color: #ebebeb;
  margin-left: 1rem;
}

.card-total button {
  border: none;
  font-size: 1rem;
  padding: .8rem 2rem;
  color: #fff;
  background-color: #000000;
  text-transform: uppercase;
  margin-top: 1rem;
  border-radius: 0.5rem;
  margin-right: 1rem;
  cursor: pointer;
  letter-spacing: 1px;
  margin-bottom: 2rem;
}

/* hover effects  */
.fa-trash-alt {
  color: #ed4848;
}

.fa-plus:hover {
  color: #42c157;
}

.fa-minus:hover {
  color: #ffb800;
}

.fa-plus,
.fa-minus {
  color: #7c7a7a;
}

.card-total button:hover {
  background-color: #9e9e9e;
  color: #000000;
  box-shadow: black 0px 0px 5px 3px;
  font-weight: 900;
  
}




/* responsive media queries  */

@media (max-width: 968px) {
  html {
    font-size: 80%;
  }
}

@media (max-width: 768px) {
  

  .items-info {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
  }

  .title,
  .add-minus-quantity,
  .price,
  .remove-item {
    padding-left: 2rem;
  }

  .items-info .product-img {
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
  }

  .items-info .product-img img{
    width: 15rem;
    height: 12rem;
  }
  
  .items-info .title{
    /* text-align: center; */
    align-items: center;
  }
  .items-info .title P{
    align-items: center;
    /* text-align: right; */
    /* width: 100%; */
    padding-left:30% ;

  }

  .add-minus-quantity {
    margin: 2rem 0 2rem 0;
    justify-content:center;
  }

  .price {
    justify-content:center;
    margin-bottom: 2rem;
  }

  .price h3::before {
    content: "Price: ";
  }

  .remove-item {
    justify-content:center;
    margin-right: 0px;
  }

  .card-total {
    margin-bottom: 2rem;
    text-align: center;
  }

  .card-total button {
    margin-bottom: 3rem;
  }
}
